html, body, #root {
  height: 100%;
}

html {
  transform: translate3d(0,0,0);
  -webkit-transform: translate3d(0,0,0);
}

.App {
  height: 100%;
  flex-direction: column;
  background-image: url("images/home-background.jpg");
  background-size: cover;
  background-position: center;
  position: static;
  overflow: auto;
  overflow-x: hidden;
}